.menu-bar {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  bottom: 0;
  width: 70px;
  padding: 8px;
  background: linear-gradient(to right top, #6a5af9, #d66efd);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  transition: all 0.3s ease;
  font-family: "Be Vietnam Pro", sans-serif;
}

.menu-bar ul li {
  overflow: hidden;
  height: 48px;
  padding: 16px;
  display: flex;
  align-items: center;
  border-radius: 6px;
  transition: background 0.3s ease;
  margin-bottom: 8px;
  font-size: 14px;
}

.menu-bar ul li span {
  min-width: 100px;
  overflow: hidden;
  display: none;
  padding-left: 20px;
  animation: menu-option 0.75s ease;
}

@keyframes menu-option {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.toggle-menu {
  cursor: pointer;
  transition: all 0.75s ease;
}
.toggle-menu.active {
  transform: rotate(-180deg);
}

.menu-bar ul li span.active {
  display: block;
}

.icon-menu-bar {
  width: 18px;
  height: 18px;
}
.menu-bar ul li:hover {
  color: var(--background);
  background-color: var(--text);
}
.item-option {
  cursor: pointer;
}
.item-option.active {
  color: var(--background);
  background-color: var(--text);
}
.menu-bar.active {
  width: 192px;
}
@media only screen and (max-width: 640px) {
  .menu-bar {
    width: 100%;
    bottom: 0;
    top: auto;
    right: 0;
    height: 60px;
    border-radius: 0;
    border-top-left-radius: 16px;
    border-top-right-radius: 16px;
  }
  .menu-bar.active {
    height: 120px;
    width: 100%;
  }
  .toggle-menu {
    display: none;
    transform: rotate(-90deg);
    top: -10px;
    right: 20px;
  }
  .toggle-menu.active {
    transform: rotate(-270deg) !important;
  }
  .menu-bar ul li {
    padding: 16px;
    margin-bottom: 0;
    margin-left: 6px;
  }
}
