.description {
  background-color: #4158d0;
  background-image: linear-gradient(
    43deg,
    #4158d0 0%,
    #c850c0 46%,
    #ffcc70 100%
  );
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}
.description p {
  width: 100%;
  color: transparent;
}
.account-contact {
  font-family: "Be Vietnam Pro", sans-serif;
}
.account-contact .social-media-contact {
  display: flex;
  align-items: center;
}
.account-contact .social-media-contact a {
  width: 34px;
  height: 34px;
  font-size: 22px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  margin-left: 12px;
}

.account-contact .social-media-contact a:nth-child(1) {
  margin-left: 0;
  background-color: #0093e9;
  background-image: linear-gradient(160deg, #0093e9 0%, #80d0c7 100%);
}
.account-contact .social-media-contact a:nth-child(2) {
  background-color: #4158d0;
  background-image: linear-gradient(
    43deg,
    #4158d0 0%,
    #c850c0 46%,
    #ffcc70 100%
  );
}
.account-contact .social-media-contact a:nth-child(3) {
  background-color: #000;
  color: #fff;
}
.love:hover {
  animation: beat 0.25s infinite alternate;
}
@keyframes beat {
  to {
    transform: scale(1.4);
  }
}
.amount-love {
  font-size: 12px;
  min-height: 32px;
  min-width: 32px;
  text-align: center;
  padding: 8px 4px;
  background-color: white;
  border-radius: 10px;
  font-weight: 500;
  margin-left: 10px;
  color: #ef4444;
}
