.card-style {
  position: relative;
  padding: 16px 18px;
  border-radius: 10px;
  border: 1px solid var(--border-color);
  z-index: 10;
}

.card-ui {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 200px;
  position: relative;
  overflow-x: hidden;
  overflow-y: hidden;
  border-bottom: 1px solid var(--border-color);
}
.card-style h4 span {
  font-family: "Be Vietnam Pro", sans-serif;
}
