@import url("https://fonts.googleapis.com/css2?family=Be+Vietnam+Pro:wght@400;500;600;700&family=Fira+Code:wght@400;500;600&family=Poppins:wght@300;400;500;600&family=Tilt+Warp&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --light-gray: #b2bec3;
  --text: #2c3e50;
  --background: #ecf0f1;
  --input-border: #6a5af9;
  --menu-bar: #6a5af9;
  --code-block-light: #1e293b;
  --border-color: #1c2739;
  --preview: #0f172a;
  /* scroll */
  --track-color: #1c2739;
  --thumb-color: #6a5af9;
  --thumb-hover-color: #888;
  --nofi-color: #b53471;
}
[data-theme="dark"] {
  --text: #ecf0f1;
  --background: #0f172a;
}

/* custom scroll */
::-webkit-scrollbar {
  width: 4px;
  height: 3px;
}

::-webkit-scrollbar-track {
  background: var(--track-color);
}

::-webkit-scrollbar-thumb {
  background: var(--thumb-color);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--thumb-hover-color);
}

html {
  scroll-behavior: smooth;
}
body {
  width: 100%;
  height: 100vh;
  overflow: hidden;
}
.App {
  font-family: "Poppins", sans-serif;
  background-color: var(--background);
  color: var(--text);
  width: 100%;
  height: 100vh;
  overflow-y: scroll;
  scroll-behavior: smooth;
}
.shadow-style {
  box-shadow: rgba(0, 0, 0, 0.14) 0px 2px 8px;
}
/* Hide scrollbar for Chrome, Safari and Opera */
.hidden-scroll::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.hidden-scroll {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
.firebase-emulator-warning {
  display: none;
}
.title {
  font-family: "Be Vietnam Pro", sans-serif;
  font-weight: 500;
}

/* custom notifications */
.rnc__notification-item--success {
  background-color: #fff !important;
  color: #28a745 !important;
}
.rnc__notification-item--success .rnc__notification-title {
  color: #28a745 !important;
}
.rnc__notification-item--success .rnc__notification-message {
  color: #28a745 !important;
}

.rnc__notification-item--danger {
  background-color: #fff !important;
  color: #ee5253 !important;
}
.rnc__notification-item--danger .rnc__notification-title {
  color: #ee5253 !important;
}
.rnc__notification-item--danger .rnc__notification-message {
  color: #ee5253 !important;
}

.rnc__notification-item--warning {
  background-color: #fff !important;
  color: #feca57 !important;
}
.rnc__notification-item--warning .rnc__notification-title {
  color: #feca57 !important;
}
.rnc__notification-item--warning .rnc__notification-message {
  color: #feca57 !important;
}

.title-nav {
  font-family: "Tilt Warp", cursive;
}
