.text-field-sys {
  padding: 15px;
  border: 1px solid var(--input-border);
  border-radius: 8px;
  position: relative;
  background-color: var(--background);
}

.text-field-sys label {
  display: inline-block;
  font-weight: 600;
  cursor: pointer;
  margin-bottom: 10px;
  color: var(--text);
  font-family: "Be Vietnam Pro", sans-serif;
}

.text-field-sys input {
  font-family: "Be Vietnam Pro", sans-serif;
  display: block;
  width: 100%;
  color: var(--text);
  font-size: 14px;
  background-color: transparent;
  outline: none;
}
.input-account label {
  position: absolute;
  font-weight: 400;
  font-size: 12px;
  color: var(--input-border);
  padding: 0 6px;
  top: -20%;
  transition: top 0.3s ease;
  background-color: #0a101d;
}
