.btn-select {
  display: flex;
  align-items: center;
  cursor: pointer;
  border-radius: 4px;
  border: 2px solid #2c2c54;
  transition: all 0.3s ease;
  padding: 8px;
}
.btn-select:hover {
  background-color: #2c2c54;
  color: #fff;
}
.btn-select span {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 14px;
  margin-right: 8px;
}
.value-select {
  display: none;
  position: relative;
  background-color: #2c2c54;
  color: #fff;
  margin-top: 10px;
  border-radius: 4px;
  padding: 10px 20px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
  animation: ShowValueSelect 0.75s ease;
}

.value-select ul li {
  padding: 6px;
  cursor: pointer;
  font-size: 14px;
  transition: all 0.3s ease;
}
.value-select ul li:hover {
  opacity: 0.5;
}

.value-select.active {
  display: block;
}

.icon-drop-down {
  transform: rotate(180deg);
}

@keyframes ShowValueSelect {
  from {
    opacity: 0;
    transform: translateY(200px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
