[data-theme="light"] {
  --preview: #fff;
}
.preview {
  color: var(--text);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: transparent;
  backdrop-filter: blur(5px);
  z-index: 1000;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  transition: all 0.3s ease;
}
.style-preview {
  min-width: 872px;
  background-color: #1e293b;
  transition: all 0.3s linear;
  height: 95vh;
  overflow: hidden;
  color: var(--text) !important;
  display: flex;
  align-items: center;
  flex-direction: column;
  overflow-y: scroll;
  padding: 20px;
}
.code-ui {
  border-width: 1px;
  --tw-border-opacity: 1;
  border-color: rgb(51 65 85 / var(--tw-border-opacity));
  margin-top: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 200px;
  position: relative;
  overflow-x: hidden;
  overflow-y: hidden;
  z-index: 999;
  background-color: var(--preview);
  transition: all 0.3s linear;
  border-radius: 10px;
}
.wrapper-code {
  width: 100%;
  overflow-y: auto;
  border-radius: 0.5rem;
  border-width: 1px;
  --tw-border-opacity: 1;
  border-color: rgb(51 65 85 / var(--tw-border-opacity));
  max-height: 300px;
}
@media only screen and (max-width: 840px) {
  .style-preview {
    min-width: 95%;
    width: 95%;
  }
}
