.overlay {
  backdrop-filter: blur(5px);
  animation: overlay 0.75s ease;
}
.edit-account {
  animation: showModal 0.75s ease;
}
@keyframes showModal {
  from {
    opacity: 0;
    transform: translate(-100%, -50%);
  }
  to {
    opacity: 1;
    transform: translate(-50%, -50%);
  }
}

@keyframes overlay {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
