.button-style {
  position: relative;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 18px;
  color: var(--text);
  border-radius: 8px;
  background-color: #4158d0;
  overflow: hidden;
  font-weight: 500;
  font-family: "Tilt Warp", cursive;
  font-size: 14px;
  transition: all 0.3s ease;
  min-height: 40px;
}
.primary {
  background-image: linear-gradient(
    43deg,
    #4158d0 0%,
    #c850c0 46%,
    #ffcc70 100%
  );
}
.second {
  background-color: transparent;
}
.nav {
  background-color: rgba(255, 255, 255, 0.2);
  color: var(--text);
}
.google-btn {
  background-color: #ee5253;
}

.button-copy-html:hover {
  background-color: #f97316;
  color: var(--text);
}

.button-copy-css:hover {
  background-color: #3b82f6;
  color: var(--text);
}

.button-style::after {
  position: absolute;
  content: "";
  background-color: rgba(255, 255, 255, 0.2);
  width: 0%;
  bottom: 0;
  top: 0;
  left: 0;
  border-radius: 8px;
  transition: width 0.3s ease;
}
.button-style:hover::after {
  width: 100%;
}
